
export default class BackService {
    constructor() {
        this._apiBase = 'http://localhost:4000/';
    }

    async getData(url) {    // Returns data fetched from db
        const res = await fetch(`${this._apiBase}${url}`);
        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, recieved status ${res.status}`);
        }
        return await res.json();
    }

    async getUsers() {  // Returns a fetched array of users from db
        const res = await this.getData('users');
        return await res;
    }

    async getAndFormUsersArray() {    // Returns a fully ready array for AllUsers.js
        const res = await this.getUsers();
        const arr = res.map(item => {
            const {
                id,
                data: {
                    fullName,
                    phone,
                    mail,
                    auto_num,
                    license_num
                }
            } = item;
            const arr = [id, fullName, phone, mail, auto_num, license_num];
            return arr;
        });
        return arr;
    }

    formUser(user) {     // Returns a fully ready object of user data
        const {id, type, data:{fullName, phone, mail, auto_num, license_num}} = user;
        const obj = {
            id,
            type,
            fullName,
            phone,
            mail,
            autoNum: auto_num,
            licenseNum: license_num
        };
        return obj
    }

    formRide(user) {    // Returns a fully ready array of user's rides
        const {id, rides} = user;
        const formedRides = rides.map(item => {
            const {
                type,
                data: {
                    price,
                    relationships: {
                        destination: {
                            data:{id},
                            from,
                            to
                        },
                    },
                    included: [{
                        attributes: {
                            streets: {
                                streetFrom,
                                streetTo
                            },
                            time: {
                                start,
                                end
                            }
                        }
                    }]
                }
            } = item;
            const obj = {id, type, price, from, to, streetFrom, streetTo, start, end};
            try {
                const {
                    data: {
                        driver: {
                            user: {  
                                fullName
                            }
                        },
                    }
                } = item;
                obj.fullName = fullName
            } catch(e){};
            
            return obj;
        });
        return formedRides;
    }

    formTransactions(user) {        // Returns a ready-to-use object of user's transactions
        const {transactions} = user;
        const formedTransactions = transactions.map(item => {
            const {
                id,
                type,
                date,
                time,
                value,
                user: {
                    fullName
                }
            } = item;
            const obj = {id, type, date, time, value, fullName};
            return obj;
        });
        return formedTransactions;
    }

    

    async getUserData(userId) {     // Return a fully ready array of user's data & rides
        const res = await this.getData(`users/${userId}`);
        const arr = [];
        arr.push(this.formUser(res));
        arr.push(this.formRide(res));
        arr.push(this.formTransactions(res));
        return arr;
    }

    async getAllTransactionsData() {
        const res = await this.getData(`transactions`);
        return res;
    }


}





