const setCompaniesInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const companiesPhotoLoaded = (file) => {
    return {
        type: 'COMPANIES_PHOTO_LOADED',
        file
    }
}

const companiesInputChange = (value) => {
    return {
        type: 'COMPANIES_INPUT_CHANGE',
        value
    }
}

const companiesPublishPost = (params = null) => {
    return {
        type: 'COMPANIES_PUBLISH_POST',
        params
    }
}

const companiesUpdatePost = (id) => {
    return {
        type: 'COMPANIES_UPDATE_POST',
        id
    }
}

const companiesDeletePost = (id) => {
    return {
        type: 'COMPANIES_DELETE_POST',
        id
    }
}

export {
    setCompaniesInitialTouched,
    companiesPhotoLoaded,
    companiesInputChange,
    companiesPublishPost,
    companiesUpdatePost,
    companiesDeletePost
}