import React from 'react';
import './transPlate.scss';

const TransPlate = ({data, url}) => {
    const { type, date, time, value, fullName } = data;

    return (
        <div className="info-plate">
            <div className="plate-photo__wrapper">
                <img src={url} alt="plate" className="plate-img"/>
            </div>
            <div className="text-content__wrapper">
                <span className="plate-name">
                    <a href="#">{fullName}</a>
                </span>
                <span className="plate-date">{`${date} в ${time}`}</span>
            </div>
            <div className={`plate-pricing ${type}`}>
                <span>{`${type === 'income' ? '+' : '-'} ${value} руб`}</span>
            </div>
        </div>
    )
}

export default TransPlate;