import React from 'react';
import { connect } from 'react-redux';
import {
    setInitialTouched,
    chatPhotoLoaded,
    chatInputChange,
    chatPublishPost,
    chatUpdatePost,
    chatDeletePost
} from '../../../actions/adsActions/inChatTab';
import AdsBlock from '../AdsBlock';

const InChatContent = ({
    adsList,
    isTouched,
    setInitialTouched,
    chatPhotoLoaded,
    chatInputChange,
    chatPublishPost,
    chatUpdatePost,
    chatDeletePost
}) => {
    const actionControllers ={
        setTouched: setInitialTouched,
        setPhotoLoaded: chatPhotoLoaded,
        inputChangeFunc: chatInputChange,
        publishPost: chatPublishPost,
        updatePost: chatUpdatePost,
        deletePost: chatDeletePost
    }

    return (
        <AdsBlock
            adsList={adsList}
            isTouched={isTouched}
            actionControllers={actionControllers}
        />
    )
}

const mapStateToProps = (state) => {
    const {adsInChatTab} = state;
    return {
        adsList: adsInChatTab.adsList,
        isTouched: adsInChatTab.isInitialTouched
    }
}

const mapDispatchToProps = {
    setInitialTouched,
    chatPhotoLoaded,
    chatInputChange,
    chatPublishPost,
    chatUpdatePost,
    chatDeletePost
}

export default connect(mapStateToProps, mapDispatchToProps)(InChatContent);