import React from 'react';
import BreadListItem from './BreadListItem';
import './breadlist.scss';

const BreadList = ({path}) => {

    console.log(path);
    const formedPath = path.split('/');
    formedPath.pop();
    console.log(formedPath);

    const elements = formedPath.map((item, i) => {
        const isActive = i === 0 ? true : false;
        return (
            <BreadListItem 
                active={isActive}    
                key={i}>{item}</BreadListItem>
        )
    })    
    return (
        <ul className="breadcrumbs__list">
            {elements}
        </ul>
    )
}

export default BreadList;