import React from 'react';
import { useLocation } from 'react-router-dom';
import NavListItem from './NavListItem';
import users from './icons/users.svg';
import chat from './icons/chat.svg';
import transactions from './icons/transactions.svg';
import mail from './icons/mail.svg';
import sales from './icons/sales.svg';
import ads from './icons/ads.svg';
import admins from './icons/admins.svg';
import stats from './icons/stats.svg'

const TabbarNav = () => {
    const icoArr = [users, chat, transactions, mail, sales, ads, admins, stats];
    const textArr = [
        'Пользователи', 
        'Сообщения', 
        'Транзакции', 
        'Рассылка', 
        'Акции', 
        'Реклама', 
        'Администраторы', 
        'Статистика'
    ];
    const location = useLocation();    
    
    
    

    const elements = textArr.map((item, i) => {
        // location which page is opened
        const urlName = location.pathname.match(/[^admin]\w*/g)[1].substring(1); 
        // location which page to go to
        const urlTo = icoArr[i].match(/\/\w*/gi)[2].slice(1);
        // ACTIVE
        const isActive = urlName === urlTo ? 'active' : '';  
        
        return (
            <NavListItem
                key={i}
                id={i}
                isActive={isActive}
                icon={icoArr[i]}
                url={urlTo}
                >{item}</NavListItem>
        )
    })
    return (
        <nav className="tabbar__nav">
            <ul className="nav__list">
                {elements}
            </ul>
         </nav>
    )
}

export default TabbarNav;