import React from 'react';
import { connect } from 'react-redux';
import {
    setInitialTouched,
    mainPhotoLoaded,
    mainInputChange,
    mainPublishPost,
    mainUpdatePost,
    mainDeletePost
} from '../../../actions/adsActions/mainTab';
import AdsBlock from '../AdsBlock';

const MainTabContent = ({
    adsList,
    isTouched,
    setInitialTouched,
    mainPhotoLoaded,
    mainInputChange,
    mainPublishPost,
    mainUpdatePost,
    mainDeletePost
}) => {
    const actionControllers ={
        setTouched: setInitialTouched,
        setPhotoLoaded: mainPhotoLoaded,
        inputChangeFunc: mainInputChange,
        publishPost: mainPublishPost,
        updatePost: mainUpdatePost,
        deletePost: mainDeletePost
    }
    return (
        <AdsBlock
            adsList={adsList}
            isTouched={isTouched}
            actionControllers={actionControllers}
        />
    )
}


const mapStateToProps = (state) => {
    const {adsMainTab} = state;
    return {
        adsList: adsMainTab.adsList,
        isTouched: adsMainTab.isInitialTouched
    }
}

const mapDispatchToProps = {
    setInitialTouched,
    mainPhotoLoaded,
    mainInputChange,
    mainPublishPost,
    mainUpdatePost,
    mainDeletePost
}

export default connect(mapStateToProps, mapDispatchToProps)(MainTabContent);