import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';
import store from './store';
import BackServiceContext from './components/BackServiceContext';
import BackService from './services/BackService';
import App from './components/App/App';


import './assets/styles/scss/reset.scss';
import './assets/styles/scss/font-faces.scss';

const backService = new BackService();

ReactDOM.render(
	<React.StrictMode>
		<Provider store={store}>
			<BackServiceContext.Provider value={backService}>
				<Router>
					<App />
				</Router>
			</BackServiceContext.Provider>
		</Provider>
	</React.StrictMode>,
  document.getElementById('root')
);
