const setForAllInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const forAllPhotoLoaded = (file) => {
    return {
        type: 'FOR_ALL_PHOTO_LOADED',
        file
    }
}

const forAllInputChange = (value) => {
    return {
        type: 'FOR_ALL_INPUT_CHANGE',
        value
    }
}

const forAllPublishPost = (params = null) => {
    return {
        type: 'FOR_ALL_PUBLISH_POST',
        params
    }
}

const forAllUpdatePost = (id) => {
    return {
        type: 'FOR_ALL_UPDATE_POST',
        id
    }
}

const forAllDeletePost = (id) => {
    return {
        type: 'FOR_ALL_DELETE_POST',
        id
    }
}

export {
    setForAllInitialTouched,
    forAllPhotoLoaded,
    forAllInputChange,
    forAllPublishPost,
    forAllUpdatePost,
    forAllDeletePost
}