import React from 'react';
import './contentTabs.scss';

const ContentTabs = ({tabs, activeTab, handleClick}) => {
    
    const content = tabs.map((item, i) => {
        const isActive = i === +activeTab ? 'active' : '';
        return (
            <div 
                key={i}
                id={i}
                onClick={handleClick}
                className={`trip-type-item ${isActive}`}><span>{item}</span></div>
        )
    });
    return (
        <div className="trip-type__wrapper">
            {content}
        </div>
    )
}

// ActiveTab should be a NUM 0-* (number of tabs)

export default ContentTabs;