import React from 'react';
import FileUploadBlock from '../FileUploadBlock';
import './uploadForm.scss';


const UploadForm = ({
    classNames = '', 
    id,
    example = null,
    handleRedirect = null, 
    photoId, 
    photoUrl,
    setPhotoLoaded,
    handleClick = null,
    disabled = false, 
    children}) => {

    return (
        <div 
            id={id}
            className={`upload-form-container ${classNames}`}
            onClick={handleClick}
            >
            <div className="left-part">
                <FileUploadBlock
                    handleClick={handleRedirect}
                    setPhotoLoaded={setPhotoLoaded}
                    photoId={photoId}
                    photoUrl={photoUrl}
                    example={example}
                    disabled={disabled}/>
            </div>
            <div className="right-part">
                <div className="text-wrapper">
                    <div className="text-left">
                        <span>Рекомендуемый размер:</span>
                        <span>1122 х 552</span>
                    </div>
                    <div className="text-right">
                        <span>Формат:</span>
                        <span>Jpeg, Png</span>
                    </div>
                </div>
                {children}
            </div>
        </div>
    )
}

export default UploadForm;