import { combineReducers } from "redux";
import userReducer from './UserReducer';
import transactionReducer from './TransactionReducer';
import salesReducer from "./SalesReducer";
import adsMainTabReducer from "./AdsReducer/mainTab";
import adsInChatTabReducer from './AdsReducer/inChatTab';
import adsTabReducer from './AdsReducer/adsTab';

export default combineReducers({
    user: userReducer,
    transaction: transactionReducer,
    sales: salesReducer,
    adsMainTab: adsMainTabReducer,
    adsInChatTab: adsInChatTabReducer,
    adsTab: adsTabReducer
});