const initialState = {
    users: [],
    userData: [{}, [], []],
    searchValue: ''
}


const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'USERS_REQUESTED':
            return {
                ...state,
                users: state.users
            }
        case 'USERS_LOADED':
            return {
                ...state,
                users: action.users
            }
        case 'USER_DATA_REQUESTED':
            return {
                ...state,
                userData: state.userData
            }
        case 'USER_DATA_LOADED':
            return {
                ...state,
                userData: action.userData
            }
        case 'SEARCH_VALUE_CHANGED':
            return {
                ...state,
                searchValue: action.value
            }
        default:
            return state;
    }
}

export default userReducer;