import React, { useState } from 'react';
import './fileUploadInput.scss';


const FileUploadInput = ({
    value = '',
    placeholder = '', 
    disabled = false,
    handleFocus, 
    handleClick, 
    textInput,
    inputChangeFunc}) => {
    const [inputValue, setInputValue] = useState(value);
    
    function handleChange(e) {
        setInputValue(e.target.value);
        inputChangeFunc(e.target.value);
    }

    
    return (
        <div className="text-input-wrapper">
            <InputRefComponent
                handleFocus={handleFocus}
                handleClick={handleClick}
                handleChange={handleChange}
                inputValue={inputValue}
                placeholder={placeholder}
                ref={textInput}
                value={value}
                disabled={disabled}
            />
        </div>
    )
}


const InputRefComponent = React.forwardRef((props, ref) => {
    const {handleFocus, handleClick, handleChange, inputValue, placeholder, disabled, value = ''} = props;
    
    
    return (
        <input
            ref={ref} 
            type="text" 
            name="file__upload-title" 
            placeholder={placeholder}
            maxLength={50}
            onFocus={handleFocus}
            onClick={handleClick}
            onChange={handleChange}
            value={inputValue}
            disabled={disabled && value ? true : false}
            autoComplete="off"
        />
    )
});





export default FileUploadInput;
