import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { useHistory} from 'react-router-dom';
import { usersRequested, usersLoaded } from '../../../actions/userActions';
import WithBackService from '../../../components/HOC';
import Row from '../../../components/AllUsers__row/';
import SearchInput from '../../../components/Inputs/';

import './allUsers.scss';



const AllUsers = ({BackService, usersRequested, usersLoaded, users, searchValue=''}) => {
    const [isLoading, setLoading] = useState(false);
    const history = useHistory();
    const defaultDataArr = [
        'id', 
        'ФИО пользователя', 
        'Телефон', 
        'Почта', 
        'Рег номер', 
        'Права'
    ];
    useEffect(() => {
        const res = BackService.getAndFormUsersArray();
        setLoading(true);
        usersRequested();
        res.then(users => {
            setLoading(false);
            usersLoaded(users);
        });
    }, []);

    const result = searchUser(users, searchValue);
    
    const elements = result.map((item) => {
        const [id] = item;
        return <Row
                    key={id}
                    id={id}
                    handleClick={() => history.push(`/admin/users/${id}`)}
                    dataArr={item}
                />
    }); 
    
    function searchUser(userList, value) {
        if (value === '') {
            return userList;
        }
        
        const indexArr = userList.map((item, i) => {
            return RegExp(value, 'i').test(item) ? i : null;
        }).filter(index => {
            return index !== null;
        });

        const array = [];
        for (let i = 0; i < indexArr.length; i++) {
            array.push(userList[indexArr[i]] )
        }
        return array;
    }
    
    
    

    return (
        <main className="allUsers">
            <div className="main__title">
                <h2 className="main__heading">ПОЛЬЗОВАТЕЛИ</h2>
                <SearchInput/>
            </div>
            <Row dataArr={defaultDataArr}></Row>
            <View isLoading={isLoading} elements={elements}/>
        </main>
    )
}

const View = ({elements, isLoading}) => {
    if (isLoading) {
        return <h1>Loading</h1>
    }
    return (
        <div className="main__content">
            {elements}
        </div>
    )
}

const mapStateToProps = (state) => {
    const {user} = state;
    return {
        users: user.users,
        searchValue: user.searchValue
    }
}

const mapDispatchToProps = {
    usersRequested,
    usersLoaded
}


export default WithBackService()( connect(mapStateToProps, mapDispatchToProps)(AllUsers) );