import React, { useState } from 'react';
import { connect } from 'react-redux';
import Breadcrumps from '../../../components/Breadcrumps';
import SearchInput from '../../../components/Inputs';
import ContentTabs from '../../../components/ContentTabs';
import RideTile from '../../../components/RideTiles';
import './allRides.scss';


const AllRides = ({userData, searchValue}) => {
    const [activeTab, changeTab] = useState(0);
    const pageName = "Поездки"
    const {fullName: userName} = userData[0];
    const tabs = ['Пассажир', 'Водитель'];

    const filteredRides = filterRides(searchRide(userData[1], searchValue), activeTab);
    const rideTiles = filteredRides.map(item => {
        const {id, type} = item;
        return (
            <RideTile
                key={id}
                type={type}
                data={item}
            />
        )
    });


    function handleClick(e) {
        const target = e.currentTarget;
        changeTab(target.id);
    }

    function searchRide(ridesList, value) {
        if (value === '') {
            return ridesList;
        }
        
        return ridesList.filter((item) => {
            const {to = '', from = '', fullName = ''} = item;

            if (to.toLowerCase().indexOf(value.toLowerCase()) > -1 || 
                from.toLowerCase().indexOf(value.toLowerCase()) > -1 ||
                fullName.toLowerCase().indexOf(value.toLowerCase()) > -1)
            {
                return item;
            } else {
                return false;
            }
        })
    }

    function filterRides(ridesList, filter) {
        if (+filter === 0) {
            return ridesList.filter(item => item.type === 'passenger')
        } else if (+filter === 1) {
            return ridesList.filter(item => item.type === 'driver')
        } else {
            return ridesList;
        }
    }


    return (
        <main className="allRides">
            <Breadcrumps 
                page="Пользователи"
                url={userName}
                secondUrl={pageName}/>
            <div className="main__title">
                <h2 className="main__heading">Все поездки пользователя</h2>
                <SearchInput/>
            </div>
            <ContentTabs 
                handleClick={handleClick}
                activeTab={activeTab}
                tabs={tabs}/>
            <div className="content-container">
                {rideTiles}
            </div>
        </main>
    )
}



const mapStateToProps = (state) => {
    const {user} = state;
    return {
        userData: user.userData,
        searchValue: user.searchValue
    }
}


export default connect(mapStateToProps)(AllRides);