import React from 'react';
import AdsBlock from '../../AdsBlock';

const ContentSection = ({
    type,
    actionControllers
}) => {
    let sectionTitle = '';
    const {adsList, isTouched} = actionControllers;
    switch (type) {
        case 'all':
            sectionTitle = 'Для всех';
            break;
        case 'drivers':
            sectionTitle = 'Для водителей';
            break;
        case 'passengers':
            sectionTitle = 'Для пассажиров';
            break;
        case 'cargoCompanies':
            sectionTitle = 'Для грузоперевозчиков';
            break;
        case 'cargoClients':
            sectionTitle = 'Для клиентов грузоперевозок';
            break;
        default:
            sectionTitle = '';
            break;
    }

    return (
        <div className="inAds-content-section">
            <div className="main__title">
                <h2 className="main__heading subtitle">{sectionTitle}</h2>
            </div>
            <AdsBlock
                adsList={adsList}
                isTouched={isTouched}
                actionControllers={actionControllers}
            />
        </div>
    )
}




export default ContentSection;