import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Route, Link, useParams, useLocation, useRouteMatch } from 'react-router-dom';
import WithBackService from '../../../components/HOC';
import {
    userDataRequested, 
    userDataLoaded
} from '../../../actions/userActions';
import './driverPage.scss';
import Breadcrumps from '../../../components/Breadcrumps';
import Section from '../../../components/DriverPage__section';
import ImgBlock from '../../../components/ImgBlock';
import ContentTabs from '../../../components/ContentTabs';
import Btn from '../../../components/ActionBtns/Btn';
import InfoTile from '../../../components/InfoTiles';
import TransPlate from '../../../components/TransactionsPlate';
import Popup from '../../../components/Popups/Popup';
import man from '../../../assets/img/transaction-male.png'



const DriverPage = ({
        BackService, 
        userDataRequested, 
        userDataLoaded, 
        userData
    }) => {
        
    const tabs = ['Пассажир', 'Водитель'];
    const [activeTab, changeTab] = useState(0);
    const [isLoading, setLoading] = useState(false);
    const params = useParams();
    const location = useLocation();
    const match = useRouteMatch();
    let background = location.state && location.state.background;


    useEffect(() => {
        setLoading(true);
        userDataRequested();
        handleAction(params.id, 'user');
    }, []);

    function handleAction(id) {
        const res = BackService.getUserData(id);
        res.then(data => {
            userDataLoaded(data);
            setLoading(false);
        });
    }

    function handleClick(e) {
        const target = e.currentTarget;
        changeTab(target.id);
    }

    const {fullName, phone, mail, autoNum, licenseNum, type} = userData[0];

    const userRides = userData[1].map(item => {
        const {id,from, to, start, end} = item;
        return (
            <div 
                key={id}
                className="timetable-content__row timetable-row">
                <span>{`${from} - ${to}`}</span>
                <div className="block">
                    <span>{start}</span>
                    <span>{end}</span>
                </div>
            </div>
        )
    });


    // Depends on {type} user/driver
    const driverInfoSection = type === 'driver' ? <DriverInfoSection    
                                                    autoNum={autoNum}
                                                    licenseNum={licenseNum}/> : null;

    const banDriverBtn = type === 'driver' ? <Link to={{
                                                    pathname: `${location.pathname}/banDriver`,
                                                    state: {
                                                        background: location,
                                                        type: 'banDriver'
                                                    }
                                                    }}
                                                >
                                                    <Btn id="banDriver" classNames="menu-btn">Запретить быть водителем</Btn>
                                                </Link> : null;

    const contentTabs = type === 'driver' ? <ContentTabs handleClick={handleClick} activeTab={activeTab} tabs={tabs}/> : null;


    const userTransactions = userData[2].map((item, i) => {
        if (i > 3) {
            return null;
        }
        const {id} = item;
        return (
            <TransPlate 
                key={id}
                data={item}
                url={man}/>
        )
    });

    // If no rides found (array Rides should exist)
    const noRidesDataElement = <span>Нет данных по поездкам</span>; 

    
    return (
        <main className="driverPage">
            <Breadcrumps
                page={'Пользователи'}
                url={fullName} />
            <Section classNames="title__section">
                <div className="user-image item">
                    <ImgBlock/>
                </div>
                <div className="user-name item">
                    <h2>{fullName}</h2>
                </div>
                <div className="buttons item">
                    <Link
                        to={{
                            pathname: `${location.pathname}/deleteUser`,
                            state: {
                                background: location,
                                type: 'deleteUser'
                            }
                        }}
                    >
                        <Btn id="deleteUser" classNames="menu-btn">Удалить пользователя</Btn>
                    </Link>
                    {banDriverBtn}
                    <Link to={{
                        pathname: `${location.pathname}/changePassword`,
                        state: {
                            background: location,
                            type: 'changePassword'
                        }
                    }}>
                        <Btn id="changePassword" classNames="menu-btn">Сменить пароль</Btn>
                    </Link>
                    
                    <Link to={{
                        pathname: `${location.pathname}/editUser`,
                        state: {
                            background: location,
                            type: 'editUser',
                            data: {
                                fullName, 
                                phone, 
                                mail, 
                            }
                        }
                    }}>
                        <Btn id="editUser" classNames="menu-btn">Редактировать данные</Btn>
                    </Link>
                    <Btn id="writeMessage" classNames="menu-btn"> 
                        <Link>Написать сообщение</Link>
                    </Btn>
                </div>
            </Section>
            <Section classNames="person-data__section row">
                <h2 className="section__title">Личная информация</h2>
                <div className="tile__wrapper">
                    <InfoTile>
                        <span className="tile__title">Дата регистрации:</span>
                        <span className="tile__value">01.07.2021г</span>
                    </InfoTile>
                    <InfoTile>
                        <span className="tile__title">Номер телефона:</span>
                        <span className="tile__value">{phone}</span>
                    </InfoTile>
                    <InfoTile>
                        <span className="tile__title">Почта:</span>
                        <span className="tile__value">{mail}</span>
                    </InfoTile>
                </div>
            </Section>
            {driverInfoSection}
            <Section classNames="trip-data__section row">
                <h2 className="section__title">Поездки:</h2>
                {contentTabs}
                <div className="trip-timetable__wrapper">
                    <div className="timetable-title__row timetable-row">
                        <span>Маршрут</span>
                        <div className="block">
                            <span>Дата начала</span>
                            <span>Прибытие</span>
                        </div>
                    </div>
                    <div className="timetable-content">
                        {userRides.length === 0 ? noRidesDataElement : userRides}
                    </div>
                </div>
                <Link to={`${params.id}/rides`}>
                    <Btn classNames="all-trips-btn">Все поездки пользователя</Btn>
                </Link>
            </Section>
            <Section classNames="transactions__section row">
                <h2 className="section__title">Транзакции</h2>
                <div className="plates__wrapper">
                    {userTransactions}
                </div>
                <Link to={{
                    pathname: `${params.id}/userTrans`,
                }}>
                    <Btn>Все транзакции пользователя</Btn>
                </Link>
            </Section>
            {background && <Route exact path={`${match.url}/${location.state.type}`} children={<Popup type={location.state.type} data={location.state.data}/>} />}
        </main>
    )
}

const DriverInfoSection = ({autoNum, licenseNum}) => {
    return (
        <Section classNames="driver-data__section row">
            <h2 className="section__title">Информация водителя:</h2>
            <div className="tile__wrapper">
                <InfoTile>
                    <span className="tile__title">Регистрационный номер:</span>
                    <span className="tile__value">{autoNum}</span>
                </InfoTile>
                <InfoTile>
                    <span className="tile__title">№ водительского:</span>
                    <span className="tile__value">{licenseNum}</span>
                </InfoTile>
            </div>
        </Section>
    )
}

const mapStateToProps = (state) => {
    const {user} = state;
    return {
        userData: user.userData
    }
}

const mapDispatchToProps = {
    userDataRequested,
    userDataLoaded
}


export default WithBackService()( connect(mapStateToProps, mapDispatchToProps)(DriverPage) );



