const setInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const mainPhotoLoaded = (file) => {
    return {
        type: 'MAIN_PHOTO_LOADED',
        file
    }
}

const mainInputChange = (value) => {
    return {
        type: 'MAIN_INPUT_CHANGE',
        value
    }
}

const mainPublishPost = (params = null) => {
    return {
        type: 'MAIN_PUBLISH_POST',
        params
    }
}

const mainUpdatePost = (id) => {
    return {
        type: 'MAIN_UPDATE_POST',
        id
    }
}

const mainDeletePost = (id) => {
    return {
        type: 'MAIN_DELETE_POST',
        id
    }
}

export {
    setInitialTouched,
    mainPhotoLoaded,
    mainInputChange,
    mainPublishPost,
    mainUpdatePost,
    mainDeletePost
}