import React from 'react';
import { useField } from 'formik';
import MaskedInput from 'react-text-mask';
import './formInput.scss';

const FormInput = ({label='', placeholder=null, classNames='', ...props}) => {

    const [field, meta] = useField(props);
    const {type} = props;


    const textMask = [
        /[А-Я]|[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/,
        /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/, /[а-я]/
    ]

    const emailMaskCreate = (rawValue) => {
        const totalArrLength = 64;
        const item = /^([a-z]|[A-Z]|[0-9]|[!#$%&'*+-/=?^_`{|}~])+|\.?/;
        const maskedArray = [];
        for (let i = 0; i < totalArrLength; i++) {
            maskedArray.push(item);
        }
        return maskedArray;
    }
    const emailMask = emailMaskCreate();

    const phoneMask = [
        '+', "7", " ", "(", /[0-9]/, /[0-9]/, /[0-9]/, ")", " ", /[0-9]/, /[0-9]/, /[0-9]/, "-", /[0-9]/,/[0-9]/, "-", /[0-9]/, /[0-9]/
    ]
    
    let mask = [];

    switch (type) {
        case 'text':
            mask = textMask;
            break;

        case 'phone':
            mask = phoneMask;
            break;
        
        case 'email':
            mask = emailMask;
            break;
        default:
            break;
    }

    return (
        <div className="form__input-wrapper">
            <label htmlFor={field.name}>{label}</label>
            <MaskedInput
                className={`${classNames}`}
                mask={mask}
                placeholderChar="&nbsp;"
                {...field} {...props}
            />
            {meta.touched && meta.error ? (<div>{meta.error}</div>) : null}
            {/* <ErrorMessage component="div" name={meta.error}/> */}
        </div>
    )
    
}


export default FormInput;
