import React, { useState } from 'react';
import { useEffect } from 'react';
import './fileUploadBlock.scss';
import upload from './upload-icon.svg';


const FileUploadBlock = ({
    handleClick, 
    photoId, 
    photoUrl = null, 
    disabled = false, 
    setPhotoLoaded = null,
    example = false
}) => {
    const [selectedPhoto, setSelectedPhoto] = useState(photoUrl);

    function handleFiles(e) {
        const files = e.target.files;
        const readyFile = URL.createObjectURL(files[0]);
        setPhotoLoaded(readyFile);
        setSelectedPhoto(readyFile);
    }

    useEffect(() => {
        if (photoUrl === '' && selectedPhoto) {
            setSelectedPhoto(selectedPhoto);
        }
    }, [photoUrl, selectedPhoto])

    
    return (
        <div className="file-input-wrapper">
            {
                example ? null :
                <img src={selectedPhoto || photoUrl} className="ready-image" alt="" />
            }
            
            
            <img src={upload} alt="upload"/>
            <input 
                id={`file-input-${+photoId}`} 
                type="file" 
                name="file"
                onClick={handleClick}
                onChange={handleFiles}
                disabled={disabled && photoUrl ? true : false}
                accept=".png, .jpg, .jpeg"
            />
            <label 
                htmlFor={`file-input-${+photoId}`}
                style={disabled ? {pointerEvents: "none"} : {}}
                ></label>
            <span>Загрузить изображение</span>
        </div>
    )
}



