const setDriverInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const driverPhotoLoaded = (file) => {
    return {
        type: 'DRIVER_PHOTO_LOADED',
        file
    }
}

const driverInputChange = (value) => {
    return {
        type: 'DRIVER_INPUT_CHANGE',
        value
    }
}

const driverPublishPost = (params = null) => {
    return {
        type: 'DRIVER_PUBLISH_POST',
        params
    }
}

const driverUpdatePost = (id) => {
    return {
        type: 'DRIVER_UPDATE_POST',
        id
    }
}

const driverDeletePost = (id) => {
    return {
        type: 'DRIVER_DELETE_POST',
        id
    }
}

export {
    setDriverInitialTouched,
    driverPhotoLoaded,
    driverInputChange,
    driverPublishPost,
    driverUpdatePost,
    driverDeletePost
}