import React from 'react';
import BackServiceContext from '../BackServiceContext';

const WithBackService = () => (Wrapped) => {

    return (props) => {
        return (
            <BackServiceContext.Consumer>
                {
                    (backService) => {
                        return <Wrapped {...props} BackService={backService}/>
                    } 
                }
            </BackServiceContext.Consumer>
        )
    }
};

export default WithBackService;