const salesDataRequested = () => {
    return {
        type: 'SALES_DATA_REQUESTED'
    }
}

const setInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const salesPhotoLoaded = (file) => {
    return {
        type: 'SALES_PHOTO_LOADED',
        file
    }
}

const salesInputChange = (value) => {
    return {
        type: 'SALES_INPUT_CHANGE',
        value
    }
}

const salesAreaChange = (value) => {
    return {
        type: 'SALES_AREA_CHANGE',
        value
    }
}

const salesPublishPost = (params = null) => {
    return {
        type: 'SALES_PUBLISH_POST',
        params
    }
}

const salesUpdatePost = (id) => {
    return {
        type: 'SALES_UPDATE_POST',
        id
    }
}

const salesDeletePost = (id) => {
    return {
        type: 'SALES_DELETE_POST',
        id
    }
}

export {
    salesDataRequested,
    setInitialTouched,
    salesPhotoLoaded,
    salesInputChange,
    salesAreaChange,
    salesPublishPost,
    salesUpdatePost,
    salesDeletePost
}