import React from 'react';
import { Route, Link, useLocation, useRouteMatch } from 'react-router-dom';
import Btn from '../../components/ActionBtns/Btn';
import Popup from '../../components/Popups/Popup';
import AdminsSection from './Admins__section';
import './admins.scss';

const Admins = () => {
    const location = useLocation();
    const match = useRouteMatch();
    let background = location.state && location.state.background;
    return (
        <main className="admins-page">
            <div className="main__title">
                <h2 className="main__heading">Администраторы</h2>
                <Link to={{
                        pathname: `${location.pathname}/addAdmin`,
                        state: {
                            background: location,
                            type: 'addAdmin'
                        }
                    }}>
                        <Btn id="addAdmin" classNames="admins-btn">Добавить администратора</Btn>
                </Link>
                
            </div>
            <AdminsSection/>
            <AdminsSection/>
            <AdminsSection/>
            {background && <Route exact path={`${match.url}/${location.state.type}`} children={<Popup type={location.state.type} data={location.state.data}/>} />}
        </main>
    )
}

export default Admins;