import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './app.scss';
import Tabbar from '../Tabbar';
import AllUsers from '../../pages/Tabbar__Users/AllUsers';
import DriverPage from '../../pages/Tabbar__Users/DriverPage';
import UserTransactions from '../../pages/Tabbar__Users/UserTransactions';
import Popup from '../Popups/Popup';
import AllRides from '../../pages/Tabbar__Users/AllRides';
import AllMessages from '../../pages/Tabbar__Messages/AllMessages';
import UserMessages from '../../pages/Tabbar__Messages/UserMessages';
import Mailing from '../../pages/Tabbar__Mailing';
import AllTransactions from '../../pages/Tabbar__Transactions';
import Sales from '../../pages/Tabbar__Sales';
import Ads from '../../pages/Tabbar__Ads';
import Admins from '../../pages/Tabbar__Admins';
import Stats from '../../pages/Tabbar__Stats';


const App = () => {
    const adminBase = '/admin';
    return (
        <div className="container">
            <Tabbar/>
            <Switch>
                <Route exact path={`${adminBase}/users/`} component={AllUsers} />

                <Route path={`${adminBase}/users/:id/rides`} component={AllRides}/>
                <Route path={`${adminBase}/users/:id/userTrans`} component={UserTransactions}/>
                <Route path={`${adminBase}/users/:id`} component={DriverPage}/>
                


                <Route path={`${adminBase}/chat`} component={AllMessages}/>
                <Route path={`${adminBase}/userMessages`} component={UserMessages}/>
                <Route path={`${adminBase}/mail`} component={Mailing}/>
                <Route path={`${adminBase}/transactions`} component={AllTransactions}/>
                <Route path={`${adminBase}/sales`} component={Sales}/>
                <Route path={`${adminBase}/ads`} component={Ads}/>
                <Route path={`${adminBase}/admins`} component={Admins}/>
                <Route path={`${adminBase}/stats`} component={Stats}/>
                <Route path="/popup">
                    <Popup type='changePassword'/>
                </Route>
            </Switch>

        </div>  
    );
}

/*
    Popup types
     -deleteUser
     -banDriver
     -changePassword
     -editUser
     -addAdmin
*/

export default App;