import React from 'react';
import { connect } from 'react-redux';
import {
    setForAllInitialTouched,
    forAllPhotoLoaded,
    forAllInputChange,
    forAllPublishPost,
    forAllUpdatePost,
    forAllDeletePost
} from '../../../actions/adsActions/adsTab/forAllActions';
import {
    setDriverInitialTouched,
    driverPhotoLoaded,
    driverInputChange,
    driverPublishPost,
    driverUpdatePost,
    driverDeletePost
} from '../../../actions/adsActions/adsTab/driverActions';
import {
    setPassInitialTouched,
    passPhotoLoaded,
    passInputChange,
    passPublishPost,
    passUpdatePost,
    passDeletePost
} from '../../../actions/adsActions/adsTab/passengersActions';
import {
    setCompaniesInitialTouched,
    companiesPhotoLoaded,
    companiesInputChange,
    companiesPublishPost,
    companiesUpdatePost,
    companiesDeletePost
} from '../../../actions/adsActions/adsTab/companiesActions';
import {
    setClientsInitialTouched,
    clientsPhotoLoaded,
    clientsInputChange,
    clientsPublishPost,
    clientsUpdatePost,
    clientsDeletePost
} from '../../../actions/adsActions/adsTab/clientsActions';
import ContentSection from './ContentSection';

const InAdsContent = (props) => {
    console.log(props);
    const actionControllers ={
        forAll: {
            adsList: props.forAllAdsList,
            isTouched: props.forAllIsTouched,
            setTouched: props.setForAllInitialTouched,
            setPhotoLoaded: props.forAllPhotoLoaded,
            inputChangeFunc: props.forAllInputChange,
            publishPost: props.forAllPublishPost,
            updatePost: props.forAllUpdatePost,
            deletePost: props.forAllDeletePost
        },
        driver: {
            adsList: props.driverAdsList,
            isTouched: props.driverIsTouched,
            setTouched: props.setDriverInitialTouched,
            setPhotoLoaded: props.driverPhotoLoaded,
            inputChangeFunc: props.driverInputChange,
            publishPost: props.driverPublishPost,
            updatePost: props.driverUpdatePost,
            deletePost: props.driverDeletePost
        },
        passenger: {
            adsList: props.passAdsList,
            isTouched: props.passIsTouched,
            setTouched: props.setPassInitialTouched,
            setPhotoLoaded: props.passPhotoLoaded,
            inputChangeFunc: props.passInputChange,
            publishPost: props.passPublishPost,
            updatePost: props.passUpdatePost,
            deletePost: props.passDeletePost
        },
        companies: {
            adsList: props.companiesAdsList,
            isTouched: props.companiesIsTouched,
            setTouched: props.setCompaniesInitialTouched,
            setPhotoLoaded: props.companiesPhotoLoaded,
            inputChangeFunc: props.companiesInputChange,
            publishPost: props.companiesPublishPost,
            updatePost: props.companiesUpdatePost,
            deletePost: props.companiesDeletePost
        },
        clients: {
            adsList: props.clientsAdsList,
            isTouched: props.clientsIsTouched,
            setTouched: props.setClientsInitialTouched,
            setPhotoLoaded: props.clientsPhotoLoaded,
            inputChangeFunc: props.clientsInputChange,
            publishPost: props.clientsPublishPost,
            updatePost: props.clientsUpdatePost,
            deletePost: props.clientsDeletePost
        }

    }
    return (
        <>
            <ContentSection 
                type="all"
                actionControllers={actionControllers.forAll}
            />
            <ContentSection 
                type="drivers"
                actionControllers={actionControllers.driver}
            />
            <ContentSection 
                type="passengers"
                actionControllers={actionControllers.passenger}
            />
            <ContentSection 
                type="cargoCompanies"
                actionControllers={actionControllers.companies}
            />
            <ContentSection 
                type="cargoClients"
                actionControllers={actionControllers.clients}
            />
            
        </>
    )
}


const mapStateToProps = (state) => {
    const {adsTab: {
        forAll,
        driver,
        passenger,
        companies,
        clients
    }} = state;

    return {
        forAllAdsList: forAll.adsList,
        forAllIsTouched: forAll.isInitialTouched,
        //
        driverAdsList: driver.adsList,
        driverIsTouched: driver.isInitialTouched,
        //
        passAdsList: passenger.adsList,
        passIsTouched: passenger.isInitialTouched,
        //
        companiesAdsList: companies.adsList,
        companiesIsTouched: companies.isInitialTouched,
        //
        clientsAdsList: clients.adsList,
        clientsIsTouched: clients.isInitialTouched,
    }
}

const mapDispatchToProps = {
// ForAll Actions
    setForAllInitialTouched,
    forAllPhotoLoaded,
    forAllInputChange,
    forAllPublishPost,
    forAllUpdatePost,
    forAllDeletePost,
//end
// Driver actions 
    setDriverInitialTouched,
    driverPhotoLoaded,
    driverInputChange,
    driverPublishPost,
    driverUpdatePost,
    driverDeletePost,
//end
//Passenger actions
    setPassInitialTouched,
    passPhotoLoaded,
    passInputChange,
    passPublishPost,
    passUpdatePost,
    passDeletePost,
//end
//Companies actions
    setCompaniesInitialTouched,
    companiesPhotoLoaded,
    companiesInputChange,
    companiesPublishPost,
    companiesUpdatePost,
    companiesDeletePost,
//end
//Clients actions
    setClientsInitialTouched,
    clientsPhotoLoaded,
    clientsInputChange,
    clientsPublishPost,
    clientsUpdatePost,
    clientsDeletePost
}


export default connect(mapStateToProps, mapDispatchToProps)(InAdsContent);