import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import {setPrefix, useId} from 'react-id-generator';
import {
    salesPublishPost,
    setInitialTouched,
    salesInputChange,
    salesAreaChange,
    salesUpdatePost,
    salesDeletePost,
    salesPhotoLoaded
} from '../../actions/salesActions';
import UploadForm from '../../components/UploadForm';
import FileUploadInput from '../../components/Inputs/FileUploadInput';
import FileUploadTextArea from '../../components/Inputs/FileUploadTextArea';
import Btn from '../../components/ActionBtns/Btn';
import './sales.scss';

setPrefix('');

const Sales = ({
    salesPosts, 
    isTouched, 
    salesPublishPost, 
    salesUpdatePost, 
    setInitialTouched,
    salesInputChange,
    salesAreaChange,
    salesDeletePost,
    salesPhotoLoaded}) => {
    const textInput = useRef(null);
    const textAreaInput = useRef(null);
    
    
    const refsData = {
        textInput,
        textAreaInput
    }



    function handleFocus() {
        setInitialTouched(true);
    }

    function handleClick(e) {
        if (e.target.tagName === 'INPUT') {
            textInput.current.focus();
        } else {
            textAreaInput.current.focus();
        } 
    }

    function handlePhotoRedirect(e) {
        e.preventDefault();
        setInitialTouched(true);
    }

    const newBlock = isTouched ? <ViewModule 
                                        refsData={refsData}
                                        salesPublishPost={salesPublishPost}
                                        inputChangeFunc={salesInputChange}
                                        areaChangeFunc={salesAreaChange}
                                        setInitialTouched={setInitialTouched}
                                        setPhotoLoaded={salesPhotoLoaded}
                                    /> : null;
    
    const postedSales = salesPosts.map(item => {
        if (Object.keys(item).length === 0) {
            return null;
        }
        const {id} = item;
        return (
            <ViewModule
                key={id}
                id={id}
                data={item}
                photoId={id}
                refsData={refsData}
                salesPublishPost={salesPublishPost}
                salesDeletePost={salesDeletePost}
                salesUpdatePost={salesUpdatePost}
                setPhotoLoaded={salesPhotoLoaded}
                setInitialTouched={setInitialTouched}
                inputChangeFunc={salesInputChange}
                areaChangeFunc={salesAreaChange}
            />
        )
    })

    return (
        <main className="sales-page">
            <div className="main__title">
                <h2 className="main__heading">Акции и Бонусы</h2>
            </div>
            <div className="content-container">
                <UploadForm example handleRedirect={handlePhotoRedirect}>
                    <FileUploadInput 
                        handleFocus={handleFocus}
                        handleClick={handleClick}
                        inputChangeFunc={salesInputChange}
                        placeholder="Заголовок"/>
                    <FileUploadTextArea 
                        handleFocus={handleFocus}
                        handleClick={handleClick}
                        placeholder="Описание"/>
                </UploadForm>
            </div>
            <div className="ready-container ">
                {newBlock}
                {postedSales}
            </div>
        </main>
    )
}


const ViewModule = ({
    id, 
    refsData = {}, 
    data = {}, 
    salesPublishPost, 
    salesUpdatePost = null,
    salesDeletePost = null,
    setPhotoLoaded = null,
    setInitialTouched = null,
    inputChangeFunc = null,
    areaChangeFunc = null}) => {

    const [isEditing, setIsEditing] = useState(false);
    const {isPublished = false, inputValue = '', textareaValue = '', photoUrl} = data;
    const {textInput, textAreaInput} = refsData;
    const [postId] = useId(1, '');

    function handlePublishClick() {
        salesPublishPost(postId);
        setInitialTouched(false);
    }

    function handleSaveClick(e) {
        const parentBlock = e.target.parentElement.parentElement.parentElement;
        salesUpdatePost(parentBlock.id)
        setIsEditing(false)
    }
    
    function handleEditClick(e) {
        const tagName = e.target.tagName;
        if (isPublished && !isEditing && (tagName === 'INPUT' || tagName === 'TEXTAREA')) {
            setIsEditing(true);
        }
    }

    function handleDeleteClick(e) {
        const parentBlock = e.target.parentElement.parentElement.parentElement;
        salesDeletePost(parentBlock.id);
    }

    function handleCancelClick() {
        setIsEditing(false)
        setInitialTouched(false);
    }

    const actionBtn = !isPublished && !isEditing ? 
        <Btn handleClick={handlePublishClick}>Опубликовать</Btn> :
        <Btn handleClick={handleSaveClick}>Сохранить</Btn>

    const cancelBtn = isPublished && !isEditing ? 
        <Btn handleClick={handleDeleteClick}>Удалить</Btn> :
        <Btn handleClick={handleCancelClick} classNames="cancel-btn">Отменить</Btn> 


    return (
        <UploadForm 
            id={!id ? postId : id}
            classNames="textarea-written"
            photoId={!id ? postId : id}
            photoUrl={photoUrl}
            handleClick={handleEditClick}
            setPhotoLoaded={setPhotoLoaded}
            disabled={isPublished && !isEditing ? true : false}
            >
            <FileUploadInput 
                textInput={textInput}
                value={inputValue}
                inputChangeFunc={inputChangeFunc}
                placeholder="Заголовок"
                disabled={isPublished && !isEditing ? true : false}/>
            <FileUploadTextArea 
                textAreaInput={textAreaInput}
                areaChangeFunc={areaChangeFunc}
                value={textareaValue}
                placeholder="Описание"
                disabled={isPublished && !isEditing ? true : false}/>
            <div className="btn-container">
                {cancelBtn}
                {isPublished && !isEditing ? null : actionBtn}
            </div>
        </UploadForm>
    )

}

const mapStateToProps = (state) => {
    const {sales} = state;
    return {
        salesPosts: sales.salesPosts,
        isTouched: sales.isInitialTouched
        
    }
}

const mapDispatchToProps = {
    salesPublishPost,
    setInitialTouched,
    salesInputChange,
    salesAreaChange,
    salesUpdatePost,
    salesDeletePost,
    salesPhotoLoaded
}

// .textarea-written для тронутого блока // add to UploadForm via classNames=""
export default connect(mapStateToProps, mapDispatchToProps)(Sales);

