import React from 'react';
import './rideTile.scss';
import womanHat from '../../assets/img/WomanHat.png';
import user from '../../assets/img/user.png';
import worker from '../../assets/img/workerMan.png';
import marker from './trip-Marker.svg';
import crossedMarker from './trip-Marker-crossed.svg';

const RideTile = ({type, data}) => {
    const {fullName, from, price, to, streetFrom, streetTo, start, end} = data;
    const content = type === 'passenger' ? <PassangerView driverName={fullName}/> : <DriverView/>
    

    return (
        <div className="ride-tile">
            <div className="row">
                {content}
                <div className="content-side info-wrapper">
                    <div className="content-item">
                        <span className="item-text">Цена:</span>
                        <span className="item-text">{`${price} руб`}</span>
                    </div>
                    <div className="content-item">
                        <span className="item-text">Начало создания</span>
                        <span className="item-text">{start}</span>
                    </div>
                    <div className="content-item">
                        <span className="item-text">Прибытие</span>
                        <span className="item-text">{end}</span>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="content-side">
                    <div className="content-item address">
                        <img src={marker} alt="marker" />
                        <span className="item-text address-city">{from}</span>
                        <span className="item-text address-street">{streetFrom}</span>
                    </div>
                    <div className="content-item address">
                        <img src={crossedMarker} alt="crossedMarker" />
                        <span className="item-text address-city">{to}</span>
                        <span className="item-text address-street">{streetTo}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RideTile;

const PassangerView = ({driverName}) => {
    return (
        <div className="content-side img-wrapper">
            <img src={womanHat} alt="womanHat" />
            <div className="content-item driver-text">
                <span className="item-text">Водитель:</span>
                <span className="item-text">
                    <a href="#">{driverName}</a>
                </span>
            </div>
        </div>
    )
}

const DriverView = () => {
    return (
        <div className="content-side img-wrapper">
            <img src={womanHat} alt="womanHat" />
            <img src={user} alt="worker" />
            <img src={worker} alt="worker" />
            <img src={worker} alt="" />
        </div>
    )
}