const initialState = {
    adsList: [],
    inputValue: '',
    photoValue: null,
    isInitialTouched: false
}

const adsInChatTabReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'INITIAL_FIELDS_TOUCHED':
            return {
                ...state,
                isInitialTouched: action.boolean
            }
        case 'CHAT_PHOTO_LOADED':
            return {
                ...state,
                photoValue: action.file
            }
        case 'CHAT_INPUT_CHANGE':
            return {
                ...state,
                inputValue: action.value
            }
        case 'CHAT_PUBLISH_POST':
            const {photoValue, inputValue} = state;

            const newPost = {
                photoUrl: photoValue,
                inputValue: inputValue,
                isPublished: true,
                id: action.params
            }
            const newAdsArr = [newPost, ...state.adsList]
            return {
                ...state,
                adsList: newAdsArr,
                photoValue: null,
                inputValue: ''
            }
        case 'CHAT_UPDATE_POST':
            const index = state.adsList.findIndex(item => {
                return item.id === action.id;
            })
            const before = state.adsList.slice(0, index);
            const after = state.adsList.slice(index + 1);
            const newObj = {
                id: action.id,
                inputValue: state.photoValue,
                photoUrl: state.inputValue,
                isPublished: true
            }
            const newAdsArray = [...before, newObj, ...after]
            return {
                ...state,
                adsList: newAdsArray,
                photoValue: null,
                inputValue: ''
            }
        case 'CHAT_DELETE_POST':
            const deleteIndex = state.adsList.findIndex(item => {
                return item.id === action.id;
            });
            const beforeArr = state.adsList.slice(0, deleteIndex);
            const afterArr = state.adsList.slice(deleteIndex + 1);
            const newadsArr = [...beforeArr, ...afterArr];
            return {
                ...state,
                adsList: newadsArr,
                photoValue: null,
                salesInputValue: ''
            }
        default:
            return state;
    }
}

export default adsInChatTabReducer;