const usersRequested = () => {
    return {
        type: 'USERS_REQUESTED'
    }
}

const usersLoaded = (users) => {
    return {
        type: 'USERS_LOADED',
        users
    }
}

const userDataRequested = () => {
    return {
        type: 'USER_DATA_REQUESTED'
    }
}

const userDataLoaded = (userData) => {
    return {
        type: 'USER_DATA_LOADED',
        userData
    }
}

const searchValueChanged = (value) => {
    return {
        type: 'SEARCH_VALUE_CHANGED',
        value
    }
}

export {
    usersRequested,
    usersLoaded,
    userDataRequested,
    userDataLoaded,
    searchValueChanged
}