import React from 'react';
import { useHistory } from 'react-router-dom';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import FormInput from '../Inputs/FormInput';
import Btn from '../ActionBtns/Btn';
import './popups.scss';
import user from './user.png';
import close from './close.svg';



const Popup = ({type, classNames='', data={}}) => {
    const history = useHistory();
    let content;
    function handleBack(e) {
        e.stopPropagation();
        history.goBack();
    }
 
    if (type === 'deleteUser') {
        content = <DeleteUser handleClick={handleBack}/>
    } else if (type === 'banDriver') {
        content = <BanDriver handleClick={handleBack}/>
    } else if (type === 'changePassword') {
        content = <ChangePassword handleClick={handleBack}/>
    } else if (type === 'editUser') {
        content = <EditUser data={data} handleClick={handleBack}/>
    } else if (type === 'addAdmin') {
        content = <AddAdmin handleClick={handleBack}/>
    }
    
    return (
        <div className={`popup ${type} ${classNames} `}>
            <div className="popup__dialog">
                <div className="popup__content">
                    
                    {content}
                </div>
            </div>
        </div>
    )
}
export default Popup;

