const EditUser = ({handleClick, data}) => {
    const {fullName = null, phone: telephone = null, mail: email = null} = data;
    const [surname, userName, patronymic] = fullName.split(' ');

    const EditUserSchema = Yup.object().shape({
        surname: Yup.string()
            .required('Поле обязательно'),
        userName: Yup.string()
            .required('Поле обязательно'),
        patronymic: Yup.string()
            .required('Поле обязательно'),
        phone: Yup.string()
            .required('Поле обязательно'),
        email: Yup.string()
            .required('Поле обязательно')
    });

    function formatPhone(value) {
        const result = value.replace(/[()-\s]/g, '');
        return result;
    }
    const phone = formatPhone(telephone);
    return (
        <>
           <div 
                onClick={handleClick}
                className="popup__close">
                <img src={close} alt="popup-close"/>
            </div>
            <div className="popup-row">
                <h2 className="popup__title">Редактирование данных</h2>
                <img src={user} alt="popup-img" className="editUserImg"/>
            </div>
            <div className="popup-row">
                <Formik
                    initialValues = {{
                        surname,
                        userName,
                        patronymic,
                        phone,
                        email
                    }}
                    validationSchema={EditUserSchema}

                    onSubmit = {values => {
                        alert(JSON.stringify(values, null, 2))
                    }}
                >
                    {formik => (
                        <Form id="edit-user-form"> 
                            <FormInput
                                label="Фамилия"
                                name="surname"
                                type="text" 
                                formik={formik}/>
                            
                            <FormInput
                                label="Имя"
                                name='userName'
                                type='text'/>
                            
                            <FormInput
                                label="Отчество"
                                name="patronymic"
                                type="text" />
                            
                            <FormInput
                                label="Телефон"
                                name="phone"
                                type="phone" />
                            
                            <FormInput
                                label="Почта"
                                name="email"
                                type="email" />
                        </Form>
                    )}
                </Formik>
            </div>
            <div className="popup-row">
                <button 
                    className="popup__btn btn"
                    form="edit-user-form">
                    <span>Сохранить</span>
                </button>
            </div>
        </>
    )
}

