import React from 'react';
import { useHistory } from 'react-router-dom';
import backArrow from './backArrow.svg';
import BreadList from './BreadList';
import './breadcrumps.scss';


const Breadcrumps = ({page='', url='', ...rest}) => { // Принимает путь, делит его .split('/')
    const {secondUrl} = rest;
    const route = `${page}/${url}/${secondUrl === undefined ? '' : `${secondUrl}/`}`;
    const history = useHistory();
    function handleBack(e) {
        e.stopPropagation();
        history.goBack();
    }
    return (
        <div className="breadcrumbs__container">
            <div 
                className="back-btn"
                onClick={handleBack}>
                <img src={backArrow} alt="backArrow"/>
                <span className="back-btn__text">Назад</span>
            </div>
            <BreadList path={route}/>
        </div>
    )
}

export default Breadcrumps;