const setInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const chatPhotoLoaded = (file) => {
    return {
        type: 'CHAT_PHOTO_LOADED',
        file
    }
}

const chatInputChange = (value) => {
    return {
        type: 'CHAT_INPUT_CHANGE',
        value
    }
}

const chatPublishPost = (params = null) => {
    return {
        type: 'CHAT_PUBLISH_POST',
        params
    }
}

const chatUpdatePost = (id) => {
    return {
        type: 'CHAT_UPDATE_POST',
        id
    }
}

const chatDeletePost = (id) => {
    return {
        type: 'CHAT_DELETE_POST',
        id
    }
}

export {
    setInitialTouched,
    chatPhotoLoaded,
    chatInputChange,
    chatPublishPost,
    chatUpdatePost,
    chatDeletePost
}