import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import {
    transactionsRequested,
    transactionsLoaded
} from '../../actions/transactionActions';
import WithBackService from '../../components/HOC';
import SearchInput from '../../components/Inputs';
import ContentTabs from '../../components/ContentTabs';
import DetailedTransPlate from '../../components/TransactionsPlate/DetailedTransPlate';
import './allTransactions.scss';

const AllTransactions = ({
    BackService, 
    allTransactions,
    transactionsRequested,
    transactionsLoaded
}) => {
    const [activeTab, changeTab] = useState(0);
    const tabs = ['Все операции', 'Пополнение', 'Вывод', 'Оплата поездок'];

    //Get data onLoad
    useEffect(() => {
        transactionsRequested();
        const res = BackService.getAllTransactionsData();
        res.then(transArr => {
            transactionsLoaded(transArr);
        })
    }, [])

    // Map layout array
    const transactionPlates = allTransactions.map(item => {
        const {
            id,
            data:{
                from: {
                    id: fromId,
                    fullName: fromName
                },
                to: {
                    id: toId,
                    fullName: toName
                }
            }
        } = item;

        let moneyValue = '';
        const type = checkType(fromId, toId, moneyValue, toName);

        item.data.type = type;
        item.data.from.fromName = formName(fromName);
        return (
            <DetailedTransPlate
                key={id}
                transData={item}
            />
        )
    });

    // Filter layoutArray according to activeTab
    function filterTransactions(transList, filter) {
        const income = 'Пополнение счета',
              outcome = 'Вывод средств'

        return transList.filter(item => {
            const {
                transData: {
                    data: {
                        type: {
                            typeValue
                        }
                    }
                }
            } = item.props;
            switch (+filter) {
                case 1:
                    return typeValue === income;
                case 2: 
                    return typeValue === outcome;
                case 3:
                    return typeValue !== income && typeValue !== outcome;
                default:
                    return transList;
            }
        })
    }

    // Set the type of transaction (Назначение)
    function checkType(from, to, ...rest) {
        if (+from === +to) {
            rest[0] = '+';
            return {
                moneyValue: rest[0],
                typeValue: 'Пополнение счета'
            }
        } else if (to === -1) {
            rest[0] = '-';
            return {
                moneyValue: rest[0],
                typeValue: 'Вывод средств'
            }
        } else if (from !== to) {
            return {
                moneyValue: '',
                typeValue: formName(rest[1])
            }
        }
    }

    function formName(name) {
        const nameArr = name.split(' ');
        nameArr.pop();
        return nameArr.join(' ');
    }

    function handleTabClick(e) {
        const target = e.currentTarget;
        changeTab(target.id);
    }

    const visibleTransactions = filterTransactions(transactionPlates, activeTab);
    return (
        <main className="all-transactions-page">
            <div className="main__title">
                <h2 className="main__heading">Транзакции</h2>
                <SearchInput/>
            </div> 
            <ContentTabs 
                tabs={tabs}
                activeTab={activeTab}
                handleClick={handleTabClick}/>
            <div className="content-container">
                {visibleTransactions}
            </div>
        </main>
    )
}

const mapStateToProps = (state) => {
    const {transaction} = state;
    return {
        allTransactions: transaction.allTransactions
    }
}

const mapDispatchToProps = {
    transactionsRequested,
    transactionsLoaded
}

export default WithBackService()(connect(mapStateToProps, mapDispatchToProps)(AllTransactions));