import React, { useState, useRef } from 'react';
import {setPrefix, useId} from 'react-id-generator';
import UploadForm from '../../../components/UploadForm';
import FileUploadInput from '../../../components/Inputs/FileUploadInput';
import Btn from '../../../components/ActionBtns/Btn';

setPrefix('');

const AdsBlock = ({
    adsList = [],
    actionControllers = {},
    isTouched = false
}) => {

    const textInput = useRef(null);

    const {
        setTouched,
        setPhotoLoaded,
        inputChangeFunc,
        publishPost,
        updatePost,
        deletePost
    } = actionControllers;


    function handleFocus() {
        setTouched(true);
    }

    function handleClick(e) {
        if (e.target.tagName === 'INPUT') {
            textInput.current.focus();
        }
    }

    function handlePhotoRedirect(e) {
        e.preventDefault();
        setTouched(true);
    }

    const newBlock = isTouched ? <ViewModule
                                    textInput={textInput}
                                    setTouched={setTouched}
                                    setPhotoLoaded={setPhotoLoaded}
                                    inputChangeFunc={inputChangeFunc}
                                    publishPost={publishPost}
                                /> : null;

    const postedAds = adsList.map(item => {
        if (Object.keys(item).length === 0) {
            return null;
        }
        const {id} = item;
        return (
            <ViewModule
                key={id}
                id={id}
                data={item}
                photoId={id}
                textInput={textInput}
                setTouched={setTouched}
                setPhotoLoaded={setPhotoLoaded}
                inputChangeFunc={inputChangeFunc}
                publishPost={publishPost}
                updatePost={updatePost}
                deletePost={deletePost}
            />
        )
    })
    

    return (
        <>
            <div className="content-container">
                <UploadForm example handleRedirect={handlePhotoRedirect}>
                    <FileUploadInput 
                        placeholder="Ссылка для перехода"
                        handleFocus={handleFocus}
                        handleClick={handleClick}
                    />
                </UploadForm>
            </div>
            <div className="ready-container ">
                {newBlock}
                {postedAds}
            </div>
        </>
    )
}


const ViewModule = ({
    textInput,
    id,
    data = {},
    setTouched = null,
    setPhotoLoaded = null,
    inputChangeFunc = null,
    publishPost = null,
    updatePost = null,
    deletePost = null //commented
}) => {
    const [isEditing, setIsEditing] = useState(false);
    const {isPublished = false, inputValue = '', photoUrl} = data;
    const [postId] = useId(1, '');

    function handlePublishClick() {
        publishPost(postId);
        setTouched(false);
    }

    function handleSaveClick(e) {
        const parentBlock = e.target.parentElement.parentElement.parentElement;
        updatePost(parentBlock.id)
        setIsEditing(false)
    }

    function handleEditClick(e) {
        const tagName = e.target.tagName;
        if (isPublished && !isEditing && tagName === 'INPUT') {
            setIsEditing(true);
        }
    }

    function handleDeleteClick(e) {
        const parentBlock = e.target.parentElement.parentElement.parentElement;
        console.log(parentBlock);
        deletePost(parentBlock.id);
    }

    function handleCancelClick() {
        setIsEditing(false)
        setTouched(false);
    }

    const actionBtn = !isPublished && !isEditing ? 
        <Btn handleClick={handlePublishClick}>Опубликовать</Btn> :
        <Btn handleClick={handleSaveClick}>Сохранить</Btn>

    const cancelBtn = isPublished && !isEditing ? 
        <Btn handleClick={handleDeleteClick}>Удалить</Btn> :
        <Btn handleClick={handleCancelClick} classNames="cancel-btn">Отменить</Btn> 

    return (
        <UploadForm
            id={!id ? postId : id}
            photoId={!id ? postId : id}
            photoUrl={photoUrl}
            handleClick={handleEditClick}
            setPhotoLoaded={setPhotoLoaded}
            disabled={isPublished && !isEditing ? true : false}
            >
            <FileUploadInput 
                placeholder="Ссылка для перехода"
                textInput={textInput}
                inputChangeFunc={inputChangeFunc}
                value={inputValue}
                disabled={isPublished && !isEditing ? true : false}
            />
            <div className="btn-container">
                {cancelBtn}
                {isPublished && !isEditing ? null : actionBtn}
            </div>
        </UploadForm>
    )
}

export default AdsBlock;