import React from 'react';
import { Link } from 'react-router-dom';
import user from '../../../assets/img/user.png';
import card from './creditCard-icon.svg';
import './detailedTransPlate.scss';

const DetailedTransPlate = ({transData={}}) => {
    const {
        data: {
            type: {
                typeValue,
                moneyValue
            },
            amount,
            date,
            time,
            from: {
                id: fromId,
                fromName
            },
            to: {
                id: toId
            }
        }
    } = transData;

    const typeElement = typeValue !== 'Пополнение счета' && 
    typeValue !== 'Вывод средств' ? <Link to={`users/${toId}`}>{typeValue}</Link> : typeValue

    return (
        <div className="trans-plate-wrapper">
            <ImgViewModule typeValue={typeValue}/>
            <div className="trans-plate-part part-2">
                <span className="trans-plate-from">
                    От кого: <Link to={`users/${fromId}`}>{fromName}</Link>
                </span>
                <span className="trans-plate-toWhom">Назначение: {typeElement}</span>
                <span className="trans-plate-date">{`${date} в ${time}`}</span>
            </div>
            <div className="trans-plate-part part-3">
                <span className="trans-plate-sum">{`${moneyValue} ${amount} руб`}</span>
            </div>
        </div>
    )
}


const ImgViewModule = ({typeValue}) => {
    const imgArr = [];
    
    switch (typeValue) {
        case 'Пополнение счета':
            imgArr.push(user, card);
            break;
        case 'Вывод средств':
            imgArr.push(card, user);
            break;
        default:
            imgArr.push(user, user);
            break;
    }
    
    return (
        <div className="trans-plate-part part-1">
            <div className={`img-wrapper ${imgArr[0] === card ? 'credit-wrapper' : ''}`}>
                <img src={imgArr[0]} alt="" className={`${imgArr[0] === card ? 'credit-img' : 'human-img'}`} />
            </div>
            <div className={`img-wrapper ${imgArr[1] === card ? 'credit-wrapper' : ''}`}>
                <img src={imgArr[1]} alt="" className={`${imgArr[1] === user ? 'human-img' : 'credit-img'}`} />
            </div>
        </div>
    )
}

export default DetailedTransPlate;