const setClientsInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const clientsPhotoLoaded = (file) => {
    return {
        type: 'CLIENTS_PHOTO_LOADED',
        file
    }
}

const clientsInputChange = (value) => {
    return {
        type: 'CLIENTS_INPUT_CHANGE',
        value
    }
}

const clientsPublishPost = (params = null) => {
    return {
        type: 'CLIENTS_PUBLISH_POST',
        params
    }
}

const clientsUpdatePost = (id) => {
    return {
        type: 'CLIENTS_UPDATE_POST',
        id
    }
}

const clientsDeletePost = (id) => {
    return {
        type: 'CLIENTS_DELETE_POST',
        id
    }
}

export {
    setClientsInitialTouched,
    clientsPhotoLoaded,
    clientsInputChange,
    clientsPublishPost,
    clientsUpdatePost,
    clientsDeletePost
}