const setPassInitialTouched = (boolean) => {
    return {
        type: 'INITIAL_FIELDS_TOUCHED',
        boolean
    }
}

const passPhotoLoaded = (file) => {
    return {
        type: 'PASSENGER_PHOTO_LOADED',
        file
    }
}

const passInputChange = (value) => {
    return {
        type: 'PASSENGER_INPUT_CHANGE',
        value
    }
}

const passPublishPost = (params = null) => {
    return {
        type: 'PASSENGER_PUBLISH_POST',
        params
    }
}

const passUpdatePost = (id) => {
    return {
        type: 'PASSENGER_UPDATE_POST',
        id
    }
}

const passDeletePost = (id) => {
    return {
        type: 'PASSENGER_DELETE_POST',
        id
    }
}

export {
    setPassInitialTouched,
    passPhotoLoaded,
    passInputChange,
    passPublishPost,
    passUpdatePost,
    passDeletePost
}