import React, { useState } from 'react';
import ContentTabs from '../../components/ContentTabs';
import MainTabContent from './Main-tab__content';
import InAdsContent from './In-ads__Content';
import InChatContent from './In-Chat__content'
import GoogleAdsContent from './Google-ads__Content'; // гугл
import './ads.scss';


const Ads = () => {
    const [activeTab, changeTab] = useState(0);
    const tabs = ['На главной', 'В объявлениях', 'В чате', 'Гугл реклама'];
    let visibleTab = null;

    function handleTabClick(e) {
        const target = e.currentTarget;
        changeTab(target.id);
    }
    console.log(activeTab);
    
    
    switch (+activeTab) {
        case 0:
            visibleTab = <MainTabContent/>
            break;
        case 1:
            visibleTab = <InAdsContent/>
            break;
        case 2:
            visibleTab = <InChatContent/>
            break;
        case 3:
            visibleTab = <GoogleAdsContent/>
            break;
        default:
            visibleTab = null;
            break;
    }
    
    return (
        <main className="ads-page">
            <div className="main__title">
                <h2 className="main__heading">Реклама</h2>
            </div>
            <ContentTabs
                tabs={tabs}
                activeTab={activeTab}
                handleClick={handleTabClick}
            />
            <div className="wrapper">
                {visibleTab}
            </div>
        </main>
    )
}


export default Ads;
