import React from 'react';
import './message.scss';
import user from '../../assets/img/user.png';
import badDriver from './badDriver.png';

const Message = ({messages=0, time, classNames='', withPhoto}) => {
    const notification = messages === 0 ? null : <Notification data={messages}/>;
    const imgBlock = withPhoto ? <ImgBlock data={badDriver}/> : null;


    return (
        <div className={`row-wrapper ${classNames}`}>
            <div className={`row `}>
                <div className="img-wrapper">
                    <img className="img-item" src={user} alt="user" />
                </div>
                <div className="text-wrapper">
                    <span className="message__title">Михайлов Александр Александрович</span>
                    <span className="message__text">Здравствуйте, скажите пожалуйста, ко мне не приехала машина, что мне делать? Здравствуйте, скажите пожалуйста, ко мне не приехала машина, что мне делать? </span>
                </div>
                <div className="date-wrapper">
                    <span className="date-text">{time}</span>
                    {notification}
                </div>
            </div>
            {imgBlock}
        </div>
    )
}

const Notification = ({data}) => {
    return (
        <div className="notification-wrapper">
            <span className="notification-text">{data}</span>
        </div>
    )
}

const ImgBlock = ({data}) => {
    return (
        <div className="img-block">
            <img src={data} alt="baddriver" />
            <img src={data} alt="" />
        </div>
    )
}

export default Message;