const initialState = {
    salesPosts: [],  // Array containing objects with all sales
    salesPhoto: null,
    salesInputValue: '',
    salesAreaValue: '',
    isInitialTouched: false
}


const salesReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SALES_DATA_REQUESTED':
            return {
                ...state,
                salesPosts: state.salesPosts
            }
        case 'INITIAL_FIELDS_TOUCHED':
            return {
                ...state,
                isInitialTouched: action.boolean
            }
        case 'SALES_PHOTO_LOADED':
            return {
                ...state,
                salesPhoto: action.file
            }
        case 'SALES_INPUT_CHANGE':
            return {
                ...state,
                salesInputValue: action.value
            }
        case 'SALES_AREA_CHANGE':
            return {
                ...state,
                salesAreaValue: action.value
            }
        case 'SALES_PUBLISH_POST':
            const {salesPhoto, salesInputValue, salesAreaValue} = state;

            const newPost = {
                photoUrl: salesPhoto,
                inputValue: salesInputValue,
                textareaValue: salesAreaValue,
                isPublished: true,
                id: action.params
            }
            const newSalesArr = [newPost, ...state.salesPosts]
            console.log('Данные собраны и отправлены в массив');
            return {
                ...state,
                salesPosts: newSalesArr,
                salesPhoto: '',
                salesInputValue: '',
                salesAreaValue: ''
            }
        case 'SALES_UPDATE_POST':
            const index = state.salesPosts.findIndex(item => {
                return item.id === action.id;
            })
            const before = state.salesPosts.slice(0, index);
            const after = state.salesPosts.slice(index + 1);
            const newObj = {
                id: action.id,
                inputValue: state.salesInputValue,
                photoUrl: state.salesPhoto,
                textareaValue: state.salesAreaValue,
                isPublished: true
            }
            const newSalesArray = [...before, newObj, ...after]
            return {
                ...state,
                salesPosts: newSalesArray,
                salesPhoto: '',
                salesInputValue: '',
                salesAreaValue: ''
            }
        case 'SALES_DELETE_POST':
            const deleteIndex = state.salesPosts.findIndex(item => {
                return item.id === action.id;
            });
            const beforeArr = state.salesPosts.slice(0, deleteIndex);
            const afterArr = state.salesPosts.slice(deleteIndex + 1);
            const newsalesArr = [...beforeArr, ...afterArr];
            return {
                ...state,
                salesPosts: newsalesArr,
                salesPhoto: '',
                salesInputValue: '',
                salesAreaValue: ''
            }
        default:
            return state;
    }
}

export default salesReducer;