import React, { useState } from 'react';
import '../FileUploadInput/fileUploadInput.scss';

const FileUploadTextArea = ({
    value = '',
    placeholder = '', 
    disabled = false,
    handleFocus, 
    handleClick, 
    textAreaInput, 
    areaChangeFunc}) => {
    const [areaValue, setAreaValue] = useState(value);

    function handleChange(e) {
        setAreaValue(e.target.value);
        areaChangeFunc(e.target.value);
    }


    return (
        <div className="textarea-wrapper">
            <AreaRefComponent
                handleFocus={handleFocus}
                handleClick={handleClick}
                handleChange={handleChange}
                areaValue={areaValue}
                placeholder={placeholder}
                ref={textAreaInput}
                value={value}
                disabled={disabled}
            />
        </div>
    )
}


const AreaRefComponent = React.forwardRef((props, ref) => {
    const {handleFocus, handleClick, handleChange, areaValue, placeholder, value, disabled} = props;

    return (
        <textarea
            ref={ref} 
            type="text" 
            name="file__upload-title" 
            placeholder={placeholder}
            maxLength={1000}
            onFocus={handleFocus}
            onClick={handleClick}
            onChange={handleChange}
            value={areaValue}
            disabled={disabled && value ? true : false}
            autoComplete="off"
        />
    )
});



export default FileUploadTextArea;