import React from 'react';
import { connect } from 'react-redux';
import WithBackService from '../../../components/HOC';
import Breadcrumps from '../../../components/Breadcrumps';
import SearchInput from '../../../components/Inputs';
import TransPlate from '../../../components/TransactionsPlate';
import man from '../../../assets/img/transaction-female.png'
import './userTransactions.scss';


const UserTransactions = ({userData, searchValue}) => {
    const {fullName: userName} = userData[0];

    const visibleTransactions = searchTransaction(userData[2], searchValue);
    
    const userTransactions = visibleTransactions.map(item => {
        const {id} = item;
        return (
            <TransPlate
                key={id}
                data={item}
                url={man} />
        )
    })
    

    function searchTransaction(transList, value) {
        if (searchValue === '') {
            return transList;
        }

        return transList.filter((item) => {
            if (value.match(/^[А-Я]/)) {
                return item.fullName.indexOf(value) > -1
            } else {
                return item.fullName.toLowerCase().indexOf(value) > -1
            }
            
        })
    }

    
    return (
        <main className="AllTransactions">
            <Breadcrumps 
                page={'Пользователи'}
                url={userName}
                secondUrl={'Транзакции'}/>
            <div className="main__title">
                <h2 className="main__heading">Все Транзакции пользователя</h2>
                <SearchInput/>
            </div>
            <div className="container">
                {userTransactions}
            </div>
        </main>
    )
}

const mapStateToProps = (state) => {
    const {user} = state;
    return {
        userData: user.userData,
        searchValue: user.searchValue
    }
}

export default WithBackService()(connect(mapStateToProps)(UserTransactions));